(function (doc, win) {
  const baseWidth = 750
  const docEl = doc.documentElement
  const resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize'
  const calc = function () {
    let clientWidth = docEl.getBoundingClientRect().width
    if (!clientWidth) return
    if (clientWidth >= baseWidth) clientWidth = baseWidth
    docEl.style.fontSize = 100 * (clientWidth / baseWidth) + 'px'
  }
  if (!doc.addEventListener) return
  win.addEventListener(resizeEvt, calc, false)
  doc.addEventListener('DOMContentLoaded', calc, false)
})(document, window)



