import Mock from 'mockjs'
import API from './api'

// 新闻相关
Mock.mock(/\/api\/news-list/, 'get', API.getNewsList)
Mock.mock(/\/api\/news-detail/, 'get', API.getNewsDetail)

// 管理团队
Mock.mock(/\/api\/team/, 'get', API.getTeamInfo)

// 娱乐产品
Mock.mock(/\/api\/products/, 'get', API.getProductList)

// 投资者关系
Mock.mock(/\/api\/stock/, 'get', API.getInvestInfo)

// 商务合作
Mock.mock(/\/api\/card/, 'get', API.getCardInfo)

// 联系我们
Mock.mock(/\/api\/contact/, 'get', API.getContactInfo)

// 轮播图
Mock.mock(/\/api\/scroll/, 'get', API.getBannerInfo)

// 发展历程
Mock.mock(/\/api\/past/, 'get', API.getPastInfo)

// 首页kv-img
Mock.mock(/\/api\/kv-img/, 'get', API.getKvImgInfo)

// 法律声明
Mock.mock(/\/api\/statement/, 'get', API.getStatementInfo)

export default Mock



